html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  color: #616070;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__img {
  margin-bottom: 36px;
  padding: 0 3px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin: 0 0 16px;
}

.card__evaluation {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  line-height: 13px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  box-sizing: border-box;
