  border: 1px solid #0000;
  border-radius: 5px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
  transition: color .5s, background-color .5s, border-color .5s;
  display: block;
}

.card:hover .card__btn {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
